import matched from './matched'

// 
const rules = {
  // "/page/1?type=a#title"  "/page/1"
  path (route, pagePath) {
    return pagePath || route.path
  },

  //  ( hash)"/page/1?type=a#title"  "/page/1?type=a"
  fullpath (route, pagePath) {
    return pagePath || route.fullPath.replace(route.hash, '')
  }
}

// 
export default {
  props: {
    //  id route
    aliveId: {
      type: [ String, Function ],
      default: 'path'
    }
  },

  mixins: [ matched ],

  methods: {
    //  id
    getAliveId (route = this.$route) {
      if (!route.matched) {
        // location  route
        route = this.$router.match(route, this.$router.currentRoute)
      }

      let rule = (route.meta && route.meta.aliveId) || this.aliveId

      if (typeof rule === 'string') {
        rule = rules[rule.toLowerCase()]
      }

      if (typeof rule !== 'function') {
        rule = rules.path
      }

      const matchRoutes = this.matchRoutes(route)

      return rule.bind(this)(route, this.getPagePath(route, matchRoutes))
    }
  }
}
